.book-item-container {
   width: 30%;
   height: 380px;
   margin: 1rem;
   border: 1px solid black;
   box-shadow: 1px 1px 1px 1px;
   display: flex;
   padding: auto;
   background-color: white;
   flex-direction: column;
   align-items: center;
   justify-content: center;
}

@media (max-width: 480px) {
   .book-item-container {
      width: 70%;
   }
}
