.new-book-controls {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-book-control label {
  font-weight: bold;
  color: #ffffff;
  margin-bottom: 0.5rem;
  display: block;
}

.new-book-actions {
  text-align: right;
}

.input-control {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}
