@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
}

.login-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-box {
  border: 1px solid rgba(0, 0, 0, 0.384);
  border-radius: 10px;
  width: 600px;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.input-container {
  margin-bottom: 20px;
}

.input-control {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}


.signin-button {
  text-align: end;
  background-color: black;
  border-radius: 10px;
  color: white;
  font-size: 18px;
  padding: 10px;
  cursor: pointer;
}

.books-container {
   display: flex;
}

@media (max-width: 480px) {
   .books-container {
      flex-direction: column;
      align-items: center;
   }
}

.no-books-read {
   color: white;
}

h2,
h3 {
   text-align: center;
}

.date-container {
   width: 80%;
   background-color: rgb(29, 117, 29);
   color: white;
   display: flex;
   flex-direction: row-reverse;
   justify-content: space-around;
   border: 1px solid black;
   border-radius: 10px;
}

.book-item-container {
   width: 30%;
   height: 380px;
   margin: 1rem;
   border: 1px solid black;
   box-shadow: 1px 1px 1px 1px;
   display: flex;
   padding: auto;
   background-color: white;
   flex-direction: column;
   align-items: center;
   justify-content: center;
}

@media (max-width: 480px) {
   .book-item-container {
      width: 70%;
   }
}

.Books-filter {
   color: white;
   padding: 0 1rem;
}

.Books-filter__control {
   display: flex;
   width: 100%;
   align-items: center;
   justify-content: space-between;
   margin: 1rem 0;
}

.Books-filter label {
   font-weight: bold;
   margin-bottom: 0.5rem;
}

.Books-filter select {
   font: inherit;
   padding: 0.5rem 3rem;
   font-weight: bold;
   border-radius: 6px;
}

.chart-bar {
   height: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
}

.chart-bar__inner {
   height: 100%;
   width: 50%;
   border: 1px solid #ffffff;
   border-radius: 12px;
   background-color: #c3b4f3;
   overflow: hidden;
   display: flex;
   flex-direction: column;
   justify-content: flex-end;
}

.chart-bar__fill {
   background-color: rgb(25, 25, 99);
   height: 1rem;
   transition: all 0.3s ease-out;
}

.chart-bar__label {
   font-weight: bold;
   color: #ffffff;
   font-size: 1rem;
   text-align: center;
}

.new-max-read-control {
   font: inherit;
   padding: 0.5rem;
   border-radius: 6px;
   border: 1px solid #ccc;
   width: 5rem;
   max-width: 100%;
}

label {
   color: white;
   font-size: 1rem;
   margin-bottom: 1rem;
}
form {
   display: flex;
   margin: 3rem;

   align-items: center;
   flex-direction: column;
}

.new-book {
   background-color: rgb(29, 117, 29);
   padding: 1rem;
   margin: 2rem auto;
   width: 50rem;
   max-width: 95%;
   border-radius: 12px;
   text-align: center;
   box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-book button {
   font: inherit;
   cursor: pointer;
   padding: 1rem 2rem;
   border: 1px solid rgb(25, 25, 99);
   background-color: rgb(25, 25, 99);
   color: white;
   border-radius: 12px;
   margin-right: 1rem;
}

.new-book button:hover,
.new-book button:active {
   background-color: rgb(55, 55, 165);
   border-color: rgb(55, 55, 165);
}

.new-book button.alternative {
   color: #220131;
   border-color: transparent;
   background-color: transparent;
}

.new-book button.alternative:hover,
.new-book button.alternative:active {
   background-color: #ddb3f8;
}

.new-book-controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-book-control label {
  font-weight: bold;
  color: #ffffff;
  margin-bottom: 0.5rem;
  display: block;
}

.new-book-actions {
  text-align: right;
}

.input-control {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.container404 {
  text-align: center;
}

